.Reasons {
    padding: 0 2rem;
    display: flex;
    grid-template: 2rem;
}

.left-r {
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
}

.left-r > img {
    object-fit: cover;
}

.left-r > :nth-child(1) {
    margin-left: 10rem;
    width: 10rem;
    grid-row: 1/3;
    height: 29.4rem;
}

.left-r > :nth-child(2) {
    width: auto;
    height: 15.8rem;
    grid-column: 2/4;
}

.left-r > :nth-child(3) {
    width: 14rem;
    grid-column: 2/3;
    grid-row: 2;
}

.left-r > :nth-child(4) {
    margin-right: 12rem;
    width: 10rem;
    height: 11.2rem;
    grid-row: 2;
    grid-column: 3/4;
}

.right-r {
    flex: 1 1;
    text-transform: uppercase;
    gap: 1rem;
    display: flex;
    flex-direction: column;
}
.right-r > span {
    font-weight: bold;
    color: var(--orange);
    font-size: 2rem;
}

.right-r > div {
    color: white;
    font-size: 3rem;
    font-weight: bold;
}

.details-r {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.details-r > div {
    display: flex;
    font-size: 1rem;
    gap: 1rem;
}

.details-r > div > img {
    width: 2rem;
    height: 2rem;
}

.partners {
    display: flex;
    gap: 1rem;
}

.partners > img {
    width: 5rem;
}

.partners > img:nth-child(1) {
    width: 9rem;
    height: 4rem;
    margin-top: 7px;
}

.partners > img:nth-child(2) {
    width: 9rem;
}

@media screen and (max-width: 768px) {
    .Reasons {
        flex-direction: column;
    }

    .right-r > span {
        font-size: medium;
    }

    .right-r > div {
        font-size: xx-large;
    }

    .partners {
        justify-content: center;
        align-items: center;
    }

    .left-r {
        grid-auto-rows: auto;
        overflow: hidden;
    }

    .left-r > :nth-child(1) {
        margin: 0;
        width: 6rem;
        height: 17rem;
    }

    .left-r > :nth-child(2) {
        height: 10rem;
        width: 15rem;
    }

    .left-r > :nth-child(3) {
        width: 7rem;
        height: 6rem;
    }

    .left-r > :nth-child(4) {
        width: 7rem;
        height: 6rem;
    }

    .right-r > span {
        margin-top: 50px;
    }
}
