.hero {
    display: flex;
    justify-content: space-between;
}

.left-h {
    padding: 2rem;
    padding-top: 1.5rem;
    flex: 3;
    display: flex;
    gap: 2rem;
    flex-direction: column;
}

.right-h {
    flex: 1;
    position: relative;
    background-color: var(--orange);
}

.the-best-ad {
    margin-top: 4rem;
    background-color: #363d42;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    text-transform: uppercase;
    color: white;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.the-best-ad > span {
    z-index: 2;
}

.the-best-ad > div {
    position: absolute;
    background-color: var(--orange);
    width: 5.4rem;
    left: 8px;
    height: 80%;
    border-radius: 3rem;
    z-index: 1;
}

.hero-text > div:nth-of-type(3) {
    font-size: 1rem;
    font-weight: 200;
    text-transform: none;
    letter-spacing: 1px;
    width: 70%;
    z-index: 1;
}

.figures {
    display: flex;
    gap: 2rem;
}

.figures > div {
    display: flex;
    flex-direction: column;
}

.figures > div > span:nth-of-type(1) {
    color: white;
    font-size: 3rem;
    margin: 0 auto;
}

.figures > div > span:nth-of-type(2) {
    color: var(--gray);
    text-transform: none;
    font-size: 1.3rem;
}

.hero-buttons {
    display: flex;
    gap: 1rem;
    font-weight: normal;
}

.hero-buttons > :nth-child(1) {
    color: white;
    background-color: var(--orange);
    width: 10rem;
    height: 3.5rem;
    font-size: 1rem;
}

.hero-buttons > :nth-child(2) {
    color: white;
    background-color: transparent;
    width: 10rem;
    border: 2px solid var(--orange);
    font-size: 1rem;
}

.hero-buttons > :nth-child(1):hover,
.hero-buttons > :nth-child(2):hover {
    transform: scale(1.1);
}

.right-h > .btn {
    position: absolute;
    right: 3rem;
    top: 2rem;
    color: black;
}

.heart-rate > img {
    width: 2rem;
}

.heart-rate {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: var(--darkGrey);
    width: fit-content;
    padding: 1rem;
    align-items: flex-start;
    border-radius: 5px;
    position: absolute;
    right: 4rem;
    top: 7rem;
}

.heart-rate > :nth-child(2) {
    color: var(--gray);
}

.heart-rate > :nth-child(3) {
    color: white;
    font-size: 1.5rem;
}

.hero-image {
    position: absolute;
    top: 12.2rem;
    right: 14rem;
    width: 28rem;
    z-index: 2;
}

.hero-image-back {
    position: absolute;
    top: 20rem;
    z-index: 1;
    width: 15rem;
}

.hero-text-info {
    font-size: 1.7rem;
}

.calories {
    display: flex;
    gap: 2rem;
    background-color: var(--caloryCard);
    border-radius: 5px;
    padding: 1rem;
    width: fit-content;
    position: absolute;
}

.calories > img {
    width: 5rem;
}

.calories > div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.calories > div > :nth-child(1) {
    color: var(--gray);
}

.calories > div > :nth-child(2) {
    color: white;
    font-size: 1.5rem;
}

.hero-blur {
    width: 22rem;
    height: 30rem;
    left: 0;
}

@media screen and (max-width: 768px) {
    .hero {
        flex-direction: column;
    }

    .hero-blur {
        width: 14rem;
    }

    .the-best-ad {
        margin-top: 0;
        font-size: medium;
        align-self: center;
        transform: scale(0.8);
    }

    .hero-text {
        font-size: x-large;
        align-items: center;
        justify-content: center;
    }

    .hero-text > div:nth-of-type(3) {
        font-size: small;
        font-weight: 200;
        letter-spacing: 1px;
        text-align: center;
    }

    .hero-buttons {
        justify-content: center;
    }

    .figures > div > span:nth-of-type(1) {
        font-size: xx-large;
        font-weight: bold;
        margin: 0;
    }

    .figures > div > span:nth-of-type(2) {
        font-size: small;
        width: 100px;
    }

    .figures {
        justify-content: space-between;
    }

    .right-h {
        position: relative;
        background-color: none;
    }

    .heart-rate {
        left: 1rem;
        top: 2rem;
        z-index: 2;
    }

    .calories {
        position: relative;
        top: 5rem;
        left: 2rem;
    }

    .calories > img {
        width: 2rem;
    }

    .calories > div > :nth-child(2) {
        color: white;
        font-size: 1rem;
    }

    .hero-image {
        position: relative;
        width: 15rem;
        left: 8rem;
        top: 5.1rem;
        align-self: center;
        z-index: 3;
    }

    .hero-image-back {
        width: 15rem;
        left: 2rem;
        top: 0rem;
        z-index: 1;
    }

    .right-h > .btn {
        right: 1rem;
    }
}
